import React from 'react';
import {Table} from 'react-bootstrap';
const numToChar = {1: '一', 2: '二', 3: '三', 4: '四', 5: '五', 6: '六'};

const Result = (user) => {
  let resultPage;
  if (user.hidden) {
    resultPage = null;
  } else {
    resultPage = <div>
      <div style={styles.head}>申请结果:</div>
      <Table style={styles.result} size='sm'>
        <tbody>
          <tr>
            <td style={styles.label}>希望入学时间:</td>
            <td>{user.entranceTime}</td>
          </tr>
        </tbody>
      </Table>
      {user.results.map((result) => {
        return (
          <div key={result.id}>
            <div style={styles.subHead}>第{numToChar[result.id+1]}志愿:</div>
            <Table style={styles.result} size='sm'>
              <tbody>
                <tr>
                  <td style={styles.label}>套磁时间:</td>
                  <td>{result.time}</td>
                </tr>
                <tr>
                  <td style={styles.label}>申请学校:</td>
                  <td>{result.target}</td>
                </tr>
                <tr>
                  <td style={styles.label}>申请结果:</td>
                  <td>{result.result}</td>
                </tr>
              </tbody>
            </Table>
          </div>
        );
      })}
    </div>;
  }
  return resultPage;
};
export default Result;

const styles = {
  result: {
    marginLeft: 10,
    width: 360,
    fontSize: 14,
  },
  head: {
    marginTop: 0,
    marginBottom: 3,
    marginLeft: 5,
    fontWeight: '700',
  },
  subHead: {
    backgroundColor: '#e0e0e0',
    padding: 3,
    paddingLeft: 6,
    paddingRight: 6,
    marginLeft: 10,
    marginRight: 10,
    marginTop: -5,
    borderRadius: 5,
    fontSize: 13,
    fontWeight: '700',
  },
  label: {
    width: 100,
  },
};
