import React from 'react';
import './App.css';
import {data} from './data';
import Unit from './Unit';
import './bootstrap.min.css';


function App() {
  return (
    <div>
      {
        data.map((user) => {
          return (
            <Unit {...user} key={user.id}/>
          );
        })
      }
    </div>
  );
}

export default App;
