import React from 'react';
import {Table} from 'react-bootstrap';

const Score = (user) => {
  let userPage;
  if (user.hidden) {
    userPage = null;
  } else {
    userPage = <Table style={styles.score} size='sm'>
      <tbody>
        <tr>
          <td style={styles.label}>GPA:</td>
          <td>{user.gpa}</td>
        </tr>
        <tr>
          <td style={styles.label}>英语成绩:</td>
          <td>{user.engScore}</td>
        </tr>
        <tr>
          <td style={styles.label}>日语成绩:</td>
          <td>{user.japScore}</td>
        </tr>
        <tr>
          <td style={styles.label}>研究计划书:</td>
          <td>{user.researchProposal}</td>
        </tr>
        <tr>
          <td style={styles.label}>其他情况:</td>
          <td>{user.others}</td>
        </tr>
      </tbody>
    </Table>;
  }
  return userPage;
};
export default Score;

const styles = {
  score: {
    margin: 10,
    width: 360,
    fontSize: 14,
  },
  label: {
    width: 100,
  },
};
