import React from 'react';

const Note = (user) => {
  if (user.hidden || user.note.includes('(空)')) {
    return null;
  } else {
    return (
      <div style={styles.note}>
        <div>{user.note}</div>
      </div>
    );
  }
};

export default Note;

const styles = {
  note: {
    marginTop: -10,
    padding: 10,
    backgroundColor: '#ffe396',
    boxShadow: '1px 1px 1px #f5f5f5',
    borderRadius: 10,
    fontSize: 13,
    display: 'flex',
    fontWeight: 450,
    boxShadow: '0px 2px 3px #e5e5e5',
  },
};
