import React from 'react';

const PersonalInfo = (user) => {
  return (
    <button style={styles.info} onClick={() => user.changeState()}>
      <div style={styles.id}>{user.id}.</div>
      <div>{user.gender}生，</div>
      <div>{user.school}</div>
    </button>
  );
};

export default PersonalInfo;

const styles = {
  info: {
    color: 'white',
    backgroundColor: '#37a7ed',
    display: 'flex',
    fontWeight: '600',
    fontSize: 15,
    borderRadius: 20,
    padding: 5,
    paddingLeft: 10,
    width: 380,
    borderWidth: 0,
  },
  id: {
    borderRadius: 20,
    textAlign: 'center',
    marginRight: 5,
  },
};
